<template>
  <div>
    <!--<div class="tool-item">
      <el-switch
        v-model="setMaxSize"
        inactive-text="(kb)"
        @change="setMaxSizeChange"
        style="margin-right: 20px"
      >
      </el-switch>
      <el-input-number v-model="maxSize"
                       :disabled="!setMaxSize"
                       size="mini"
                       :min="100"
                       :max="1000"
                       :step="50"
                       @change="maxSizeChange"
                       label=""
      ></el-input-number>
    </div>-->

    <!--  -->
    <div v-if="isShowRename" class="tool-item">
      <el-switch v-model="isRename" active-text="" @change="rename()">
      </el-switch>
    </div>
    <div v-if="isShowRename && isRename" class="tool-item">
      <el-input
        v-model="newName"
        :clearable="true"
        placeholder=""
        size="small"
        @input="rename()"
      >
      </el-input>
    </div>

    <!--  -->
    <div v-if="isShowHashName" class="tool-item">
      <el-switch
        v-model="isHashRename"
        active-text=""
        @change="hashRename"
      >
      </el-switch>
    </div>

    <!-- Markdown -->
    <div v-if="isShowTransformMarkdown" class="tool-item">
      <el-switch
        v-model="isTransformMarkdown"
        active-text="Markdown"
        @change="transformMarkdown"
      >
      </el-switch>
    </div>

    <div class="tool-item operation-btn">
      <el-button icon="el-icon-refresh" plain @click="uploadReset"
        ></el-button
      >
      <el-button icon="el-icon-upload" plain type="primary" @click="uploadFile"
        ></el-button
      >
    </div>
  </div>
</template>

<script>
export default {
  name: "UploadTools",

  data() {
    return {
      setMaxSize: false,
      maxSize: 200,
      isRename: false,
      isHashRename: true,
      isTransformMarkdown: false,
      newName: '',
    }
  },

  props: {
    isShowRename: {
      type: Boolean,
      default: false
    },
    isShowHashName: {
      type: Boolean,
      default: false
    },
    isShowTransformMarkdown: {
      type: Boolean,
      default: false
    }
  },

  mounted() {
    this.hashRename(this.isHashRename)
    this.rename(this.isRename)
    this.setMaxSizeChange(this.setMaxSize)
    this.maxSizeChange(this.maxSize)
  },

  watch: {
    isShowRename(e) {
      this.isRename = false
      if (e) {
        this.newName = 'xxx'
      } else {
        this.newName = ''
      }
    }
  },


  methods: {
    // 
    rename() {
      this.$emit('rename', {
        isRename: this.isRename,
        newName: this.newName
      })
    },

    // 
    hashRename(e) {
      this.$emit('hash-named', e)
    },

    // transform markdown
    transformMarkdown(e) {
      this.$emit('transform-markdown', e)
    },

    setMaxSizeChange(e) {
      this.$emit('is-set-max-size', e)
    },

    maxSizeChange(e) {
      this.$emit('max-size', e)
    },

    // 
    uploadReset() {
      this.$emit('upload-reset')
    },

    // 
    uploadFile() {
      this.$emit('upload-file')
    }

  },
}

</script>

