<template>
  <div class="left-side-content">
    <ul class="menu-list">
      <li
        v-for="item in menuObjList"
        v-show="item.path !== '/management' || userConfigInfo.loggingStatus"
        :class="{ active: item.isActive }"
        class="menu-item"
        @click="onClickMenu(item)"
      >
        <div class="menu-content">
          <i :class="item.icon" class="menu-icon"></i>
          <span class="menu-title">{{ item.title }}</span>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import {mapGetters} from "vuex";

export default {
  name: "LeftSide",

  data() {
    return {
      menuObjList: [{
        title: '',
        icon: 'el-icon-edit',
        isActive: true,
        path: '/config'
      },
        {
          title: '',
          icon: 'el-icon-upload',
          isActive: false,
          path: '/upload'
        },
        {
          title: '',
          icon: 'el-icon-setting',
          isActive: false,
          path: '/management'
        },
        // {
        //   title: '',
        //   icon: 'el-icon-info',
        //   isActive: false,
        //   path: '/help'
        // },
      ]
    }
  },

  mounted() {
    this.initMenuActive()
  },

  watch: {
    $route(e) {
      this.menuObjList.forEach(v => v.isActive = v.path === e.path)
    }
  },

  computed: {
    ...mapGetters({
      userConfigInfo: 'getUserConfigInfo',
    }),
  },

  methods: {
    onClickMenu(event) {

      const path = event.path

      if (path === '/management') {

        if (!this.userConfigInfo.selectedRepos) {
          this.$message.warning('')
          return
        }

        if (!this.userConfigInfo.selectedDir) {
          this.$message.warning('')
          return
        }
      }
      this.$router.push(path)
    },

    initMenuActive() {
      this.menuObjList.forEach(v => v.isActive = v.path === this.$router.currentRoute.path)
    }
  }
}

</script>

<style lang="scss" scoped>
@import "src/style";

.left-side-content {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  box-shadow: 0 0 6px $shadowColor;

  .menu-list {
    .menu-item {
      width: 100%;
      height: $leftSideWidth;
      box-sizing: border-box;
      cursor: pointer;
      color: #585858;
      margin-top: 20px;
      display: flex;
      justify-content: center;
      align-items: center;

      .menu-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .menu-icon {
          font-size: 26px;
        }

        .menu-title {
          margin-top: 2px;
          font-size: 12.8px;
        }
      }

      &.active {
        background: $backgroundColor;
        color: $primaryColor;
        position: relative;
        font-weight: bold;

        &::after {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
          width: 4px;
          background: $primaryColor;
        }
      }
    }
  }
}
</style>
