import getType from "./getType";

/**
 *  object  key 
 * @param object
 */
const cleanObject = (object) => {

  for (let key in object) {
    switch (getType(object[key])) {

      case 'string':
        object[key] = ''
        break

      case 'array':
        object[key] = []
        break

      case 'number':
        object[key] = 0
        break

      case 'boolean':
        object[key] = false
        break
    }
  }
}

export default cleanObject
