<template>
  <div class="page-container config-page-container">
    <el-form label-width="70px" label-position="right" class="config-form">
      <el-form-item label="Token">
        <el-input v-model="userConfigInfo.token"></el-input>
      </el-form-item>

      <el-form-item class="operation">
        <el-button plain @click="reset()">  </el-button>
        <el-button plain type="primary" @click="getUserInfo()">
          Token
        </el-button>
      </el-form-item>
    </el-form>

    <!--  -->
    <el-form
      label-width="70px"
      label-position="right"
      v-if="userConfigInfo.token"
      v-loading="loading"
      element-loading-text="..."
    >
      <el-form-item v-if="userConfigInfo.owner" label="">
        <el-input v-model="userConfigInfo.owner" readonly></el-input>
      </el-form-item>

      <el-form-item v-if="userConfigInfo.email" label="">
        <el-input v-model="userConfigInfo.email" readonly></el-input>
      </el-form-item>

      <el-form-item v-if="userConfigInfo.reposList.length" label="">
        <el-select
          v-model="userConfigInfo.selectedRepos"
          filterable
          style="width: 100%"
          placeholder="..."
          @change="selectRepos"
        >
          <el-option
            v-for="repos in userConfigInfo.reposList"
            :key="repos.value"
            :label="repos.label"
            :value="repos.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
    </el-form>

    <!--  -->
    <el-form
      label-width="70px"
      label-position="right"
      v-if="userConfigInfo.selectedRepos"
      v-loading="dirLoading"
      element-loading-text="..."
    >
      <el-form-item
        v-if="userConfigInfo.reposList.length && userConfigInfo.selectedRepos"
        label=""
      >
        <el-radio-group
          v-model="userConfigInfo.dirMode"
          @change="dirModeChange"
        >
          <el-tooltip content="" placement="top">
            <el-radio label="newDir"></el-radio>
          </el-tooltip>

          <el-tooltip
            :content="
              ' ' + userConfigInfo.selectedBranch + ' '
            "
            placement="top"
          >
            <el-radio label="rootDir"></el-radio>
          </el-tooltip>

          <el-tooltip
            :content="' YYYYMMDD '"
            placement="top"
          >
            <el-radio label="autoDir"></el-radio>
          </el-tooltip>

          <el-tooltip
            v-if="userConfigInfo.dirList.length"
            :content="
              ' ' + userConfigInfo.selectedRepos + ' '
            "
            placement="top"
          >
            <el-radio label="reposDir"
              >{{ userConfigInfo.selectedRepos }}</el-radio
            >
          </el-tooltip>
        </el-radio-group>
      </el-form-item>

      <el-form-item
        v-if="userConfigInfo.dirMode === 'autoDir'"
        label=""
      >
        <el-input v-model="userConfigInfo.selectedDir" readonly></el-input>
      </el-form-item>

      <el-form-item v-if="userConfigInfo.dirMode === 'rootDir'" label="">
        <el-input v-model="userConfigInfo.selectedDir" readonly></el-input>
      </el-form-item>

      <el-form-item v-if="userConfigInfo.dirMode === 'newDir'" label="">
        <el-input
          v-model="userConfigInfo.selectedDir"
          @input="persistUserConfigInfo()"
          clearable
          placeholder="..."
        ></el-input>
      </el-form-item>

      <el-form-item
        v-if="
          userConfigInfo.dirList.length && userConfigInfo.dirMode === 'reposDir'
        "
        label=""
      >
        <el-select
          v-model="userConfigInfo.selectedDir"
          filterable
          style="width: 100%"
          placeholder="..."
          @change="persistUserConfigInfo"
        >
          <el-option
            v-for="repos in userConfigInfo.dirList"
            :key="repos.value"
            :label="repos.label"
            :value="repos.value"
          >
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item style="float: right" v-if="userConfigInfo.selectedRepos">
        <el-button plain type="success" @click="goUpload"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import timeHelper from "../common/utils/timeHelper";

export default {

  name: "Config",

  data() {
    return {
      loading: false,
      dirLoading: false,
    }
  },

  mounted() {

  },

  watch: {
    loggingStatus(e) {
      if (!e) {
        this.loading = false
        this.dirLoading = false
      }
    }
  },

  computed: {
    ...mapGetters({
      userConfigInfo: 'getUserConfigInfo',
      loggingStatus: 'getUserLoggingStatus',
    })
  },

  methods: {
    getUserInfo() {
      if (this.userConfigInfo.token) {
        this.loading = true
        this.$axios.get(
          '/user',
          {
            headers: {
              "Content-Type": "application/json",
              "Authorization": `token ${this.userConfigInfo.token}`
            }
          }
        ).then(res => {
          if (res && res.status === 200) {
            this.saveUserInfo(res)
            this.getReposList(res.data['repos_url'])
          } else {
            this.loading = false
          }
        })

      } else {
        this.$message.warning('Token')
      }
    },

    saveUserInfo(res) {
      this.userConfigInfo.loggingStatus = true
      this.userConfigInfo.owner = res.data['login']
      this.userConfigInfo.name = res.data['name']
      this.userConfigInfo.email = res.data['email']
      this.userConfigInfo.avatarUrl = res.data['avatar_url']
      this.persistUserConfigInfo()
    },

    getReposList(repos_url) {
      this.$axios.get(
        repos_url,
        {
          headers: {
            "Content-Type": "application/json",
            "Authorization": `token ${this.userConfigInfo.token}`
          }
        }
      ).then(res => {
        if (res.status === 200 && res.data.length) {
          this.userConfigInfo.reposList = []
          for (const repos of res.data) {
            if (!repos.fork) {
              this.userConfigInfo.reposList.push({
                value: repos.name,
                label: repos.name,
                desc: repos.description
              })
            }
          }
          this.loading = false
          this.persistUserConfigInfo()
        }
      })
    },

    selectRepos(repos) {
      this.persistUserConfigInfo()
      this.getBranchList(repos)
    },

    getBranchList(repos) {
      this.$axios.get(
        `https://api.github.com/repos/${this.userConfigInfo.owner}/${repos}/branches`,
        {
          headers: {
            "Content-Type": "application/json",
            "Authorization": `token ${this.userConfigInfo.token}`
          }
        }
      ).then(res => {

        if (res.status === 200) {

          const MASTER = 'master'
          const MAIN = 'main'

          if (res.data.length) {
            if (res.data.some(v => v.name === MASTER)) {
              this.userConfigInfo.selectedBranch = MASTER
            } else if (res.data.some(v => v.name === MAIN)) {
              this.userConfigInfo.selectedBranch = MAIN
            }
            this.getDirList(repos)
          } else {
            this.userConfigInfo.selectedBranch = MASTER
          }
        }
      })
    },

    getDirList(repos) {
      this.dirLoading = true
      this.$axios.get(
        `https://api.github.com/repos/${this.userConfigInfo.owner}/${repos}/contents`,
        {
          headers: {
            "Content-Type": "application/json",
            "Authorization": `token ${this.userConfigInfo.token}`
          }
        }
      ).then(res => {

        if (res.status === 200 && res.data.length) {
          this.userConfigInfo.dirList = [{value: '/', label: '/'}]
          for (const item of res.data) {
            if (item.type === 'dir') {
              this.userConfigInfo.dirList.push({
                value: item.name,
                label: item.name
              })
            }
          }
          this.persistUserConfigInfo()
        }
        this.dirLoading = false

      })
    },

    dirModeChange(dirMode) {
      switch (dirMode) {

        case 'rootDir':
          this.userConfigInfo.selectedDir = '/'
          break;

        case 'autoDir':
          // 
          this.userConfigInfo.selectedDir = timeHelper.getYYYYMMDD()
          break;

        case 'newDir':
          this.userConfigInfo.selectedDir = ''
          break;

        case 'reposDir':
          this.userConfigInfo.selectedDir = ''
          break;

      }
      this.persistUserConfigInfo()
    },

    persistUserConfigInfo() {
      this.$store.commit('PERSIST_USER_CONFIG_INFO')
    },

    reset() {
      this.loading = false
      this.dirLoading = false
      this.$store.dispatch('LOGOUT')
    },

    goUpload() {
      const dir = this.userConfigInfo.selectedDir
      const dirMode = this.userConfigInfo.dirMode
      let warningMessage = ''

      if (!dir) {
        switch (dirMode) {
          case 'newDir':
            warningMessage = ''
            break
          case 'reposDir':
            warningMessage = ` ${this.userConfigInfo.selectedRepos} `
            break
        }
        this.$message.warning(warningMessage)
        return
      }

      this.$router.push('/upload')
    }

  }
}
</script>

<style scoped lang="scss">
.config-page-container {
  overflow-y: auto;

  .config-form {
    .operation {
      display: flex;
      justify-content: flex-end;

      .el-button {
        margin-left: 20px;
      }
    }
  }
}
</style>
